
html, body {
	height: 100%;
	@extend .font-sans;
}

img {
	@extend .img-fluid;
}
body {
	background: $background-color;
	@extend .font-g1;
}
a {
	@extend .link-blue;
}
.container {
	background-color: $background-color;
	position: relative;
}

.col-twitter {
	color: $col-twitter;
}

.page-description {
	
	@extend [class*="item-teaser"];
	@extend .item-teaser;
	a {
		@extend .link-blue;
	}
	p {
		font-size: 1.1rem;
	}
}
.site-business {
	.header-sticky .container {
		background: transparent;
	}
	background: $col-business;	
	.header-sticky,
	.site-header .header-bottom,
	.container {
		background: $col-business;
	}

	.header-top .container, 
	.header-middle .container{
		background-color: $background-color;
	}

}
.author-avatar {
	border: .2rem solid #fff;
	box-shadow: 2px 2px 0 rgba(0,0,0,.1);
}


