[class*="topmenu-item"] {
	text-decoration: none!important;
	line-height: $headerHeight;
	font-size: 0.688rem;
	font-weight: 500;
	text-transform: uppercase;
	color: #444;
	
	.btn-circle {
		border-color: transparent;
		margin: 0;
		vertical-align: middle;
		font-size: 1rem;
	}
	&:hover, 
	&:focus {
		color: #000;
		text-decoration: none;
		i {
			color: #000;

		}
		
	}

}
.toggle-user-menu,
.toggle-login {
	.btn-circle {
		margin-right: -7px;
	}
}

.btn-circle-label-clean {
	border-color: transparent!important;
	padding: 0!important;

}

.topmenu-item-label {
	display: inline-block;
	.btn-label {
		float: left;
	}
}
body {
	padding-top: $subheaderHeight;	
}


body.default-header {
	padding-top: $headerHeight;
}


.site-header {
	.container {
		background-color: transparent;
	}
}

.header-sticky {
	@extend .font-s2;
	background: $background-color;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	height: $subheaderHeight;
	text-align: center;
	z-index: 9999;
}
.default-header .header-sticky {
	height: $headerHeight;
}




[class*="tools-menu"] {
	display: inline-block;
	position: absolute;
	top: 0;
	margin: 0;
	li {
		display: inline-block;
	}
	li + li {
		margin-left: .4rem;
	}
}
.tools-menu-left {
	left: .625rem;
	
}
.tools-menu-right {
	right: .625rem;
}

.site-header-front-desktop {
	height: 4.063rem;
	.tools-menu {
		height: $headerHeight;
	}

	[class*="topmenu-item"] {
		line-height: $headerHeight;
	}
	
	.site-logo {
		margin-top: .8rem;
		@include logo-width(12.938rem, blue);
	}
}
.hidden-label {
	.topmenu-label {
		display: none;
	}
}
.site-header-sub-desktop {
	[class*="topmenu-item"] {
		line-height: $subheaderHeight;
	}
	.tools-menu {
		height: $subheaderHeight;
	}
	

	.left-side {
		position: absolute;
		left: 0;
	}
	.site-logo {	
		@include logo-width(10.313rem, blue);
		margin-top: .61rem;
		margin-right: $spacer;
		margin-left: 1.5rem;
	}
	.tools-menu-left {
		position: relative;
		float: left;
	}
	.header-sticky {
		height: $subheaderHeight;
	}
}


.site-header-front-mobile {
	.site-logo {
		font-size: 2.5rem;
		line-height: $headerHeight;
		&:hover {
			text-decoration: none;
			color: #000;
		}
	}
}







.link-topmenu-cookies {
	color: #999;
	background: url(/dist/images/cookies.png) left center no-repeat;
	background-size: 13px;
	width: 14px;
	height: 13px;

}



.header-bottom {
	@extend .font-g1;
	font-size: 1rem;
	height: $headerButtomHeight;
	border-bottom: $border-width solid $border-color;
}
.main-navigation {
	text-align: center;
	ul {
		@extend .list-reset;
		margin-bottom: 0;
	}
	li {
		display: inline-block;
		line-height: $headerButtomHeight;
		margin: 0 .5rem 0 0;
		/*@extend .font-s2;*/
		font-weight: 400;
		
		&:last-child {
			margin-right: 0;
		}
	}
	
	
	a {
		color: #222;
		display: block;
		padding: 0 .4rem;
		&:hover {
			text-decoration: none;	
			color: $col-blue-1;
		}
	}
	.active a {
		color: $col-blue-1;
	}
	.menu-cookie a {
		line-height: $subheaderHeight;
		display: inline-block;
	}
}
.page-title {
	font-size: 2rem;
	color: $col-blue-1;
	@extend .font-sans-alt;
	line-height: $subheaderHeight;
	text-transform: uppercase;
	&:hover {
		color: darken($col-blue-1, 5%);
		text-decoration: none;
	}
}


