[class*="header-marketing"] {
	border-bottom: $border-width solid $border-color;
	text-align: left;
	line-height: 1.2rem;
	padding: .5rem 0 .4rem;
	overflow: hidden;
	@extend .font-s2;
	font-size: .75rem;
	
	.gray {
		color: #999;
	}
	a {
		color: #444;
		display: inline-block;
		position: relative;
		padding-left: 40px;
		&:before {
			width: 35px;
			height: 30px;
			content: '';
			background: url(/dist/images/packshot.png) center center no-repeat;
			background-size: 35px;
			position: absolute;
			left: 0px;
			top: -5px;
			display: inline-block;

		}
		&:hover {
			color: #000;
		}

	}
	strong {
		font-weight: 500;
	}
}
.header-marketing {
	display: none
}

.header-marketing-2 {
	/*display: none;*/
}
.site-business .logo-business {
	@extend .font-s1;
	text-transform: none;
	padding-top: .3rem;
	display: inline-block;
}
.site-business {
	.page-title,
	.main-navigation .active a,
	.main-navigation a:hover,
	.logo-business {
		color: #a41147;
	}
	
}

.mobile-user-nav {
	font-size: .7rem;
	float: right;
	display: inline-block;
	position: relative;
	z-index: 100;
	li {
		float: left;
		margin: 0;
		padding: 0;
		white-space: nowrap;
		@extend .font-s2;
		font-weight: 500;
	}
	a {
		white-space: nowrap;
		padding: 0 0 0 .4rem;
		color: #999;

	}
	li > a.toggle-user-menu  {
		text-overflow: ellipsis;
		display: block;
		overflow: hidden;
		white-space: nowrap;
		max-width: 150px;
		padding-left: .7rem;
		&:before {
			content: '\e809';
			font-family: b-icons;
			position: absolute;
			top: 0;
			left: 0;
		}
		
		
	}
}

.no-scale {
	i:before {
		transform: none!important;
	}
}



.user-menu-container {
	@extend .font-s2;
	border: $border-width solid $border-color;
	background-color: #fff;
	width: 16rem;
	padding: .625rem;
	box-shadow: 0 0 .5rem rgba(0,0,0,.2);
	position: absolute;
	top: .4rem;
	right: 0;
	font-size: .8rem;
	opacity: 0;
	transition: transform, opacity;
	transition-duration: .2s;
	transform-origin: top right;
	transform: scale(0);
	font-weight: 500;
	font-size: .8rem;

	.toggle-user-menu {
		color: #999;
		font-size: 1rem;
		position: absolute;
		top: .6rem;
		right: .6rem;
		font-size: .7rem;
		line-height: 1;
		&:hover {
			color: $col-blue-1;
		}
	}
}

.show-usermenu {
	.user-menu-container {
		opacity: 1;
		transform: scale(1);
	}
}
.list-user-menu {
	padding-left: 0;
	li {
		display: block;
		float: none;
		text-align: left;
		line-height: initial;
		font-weight: 500;
		font-size: .8rem;
		&:first-child {
			padding: 0 0 .4rem .4rem;
			border-bottom: $border-width solid $border-color;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
			padding-right: 1rem;
			padding-top: .2rem;
			i:before {
				transform: scale(1.2);
				margin-right: .2rem;
			}
		}
	}

	a:not(.btn) {
		display: block;
		color: #999;
	
		padding: .6rem .4rem;
		&:hover {
			
			background-color: lighten(#eee, 5%);
			color: $col-blue-1;
			text-decoration: none;
		}
	}
	
}
.sticky-inner {
	background-color: $background-color; 
	border-bottom: $border-width solid $border-color;
	z-index: 10;
	position: relative;
}

.site-search {
	height: $subheaderHeight + 2.2rem;
	background-color: $background-color;
	transform: translateY(-100%);
	transition: all .2s ease;
	position: relative;
	padding-top: .4rem;
	text-align: right;
	border-bottom: $border-color solid $border-width;
	z-index: 1;
	form {
		position: relative;
		margin: .5rem 0 0 auto;
		width: calc(100% - 3.2rem);
		text-align: left;
	}
	input[type="search"] {
		border: 0;
		color: #222;
		border-bottom: .1rem solid rgba(0,0,0,.2);
		padding-top: .2rem;
		height: 3.1rem;
		font-size: 1.4rem;
		width: 100%;
		background-color: transparent;
		&:focus {
			outline: 0;
			
		}
	}
	
	.btn-circle {
		/*border-color: rgba(0,0,0,.1)!important;*/
		position: absolute;
		right: 0;
		top: .625rem;
		line-height: 1;
	}
	.toggle-search {
		position: absolute;
		left: 0;
		top: 0;
		display: inline-block;
		padding: 1.4rem 1rem 0;
		font-size: 1.1rem;
		color: #999;
		&:hover {
			color: $col-blue-1;
		}
	}
}

.show-search {
	.site-search {
		transform: translateY(0);
		
		@include gradient-vertical(darken($background-color, 10%), $background-color, 0%, 5%);
	}
}

@include media-breakpoint-up(md) {
	.site-header-sub .tools-menu-left {
		position: relative;
	}
	.main-navigation .mobile-section-title {
		display: none;
	}
	
}
@include media-breakpoint-down(sm) {
	.site-header-mobile {
		height: $headerButtomHeight;
		position: relative;

		
		ul {
			height: $headerButtomHeight;
		}
		nav {
			position: relative;
		}

	}
	[class*=topmenu-item] { 
		height: $headerButtomHeight;
		line-height: $headerButtomHeight;
	}

	.topmenu-label {
		display: none;
	}
	.site-search {
		height: $subheaderHeight + 1.6rem;
		padding-top: .4rem;
		form {
			position: relative;
			margin: 0 auto 0 0;
			width: calc(100%);
			text-align: left;
		}
		input[type="search"] {
			font-size: 16px;
		}
		
		.toggle-search {
			display: none;
		}
		
	}

	.site-header-mobile {
		.site-logo {
			@include logo-width(10.313rem, blue);
			margin-top: .55rem;
		}
	}
	
	body.default-header {
		padding-top: $subheaderHeight;
		.section-nav {
			display: none;
		}
		.main-navigation {
			text-align: center;
		}
		.mobile-user-nav {
			float: none;
		}
	}
	
	[class*="tools-menu"],
	.default-header .header-sticky {
		height: $subheaderHeight;
	}
	.tools-menu-left {
		left: 0;
	}
	.tools-menu-right {
		right: 0;
	}

	.default-header .header-sticky,
	[class*="tools-menu"] {
		height: $subheaderHeight;
	}

	[class*="btn-topmenu"] {
		padding: .5rem .8rem .4rem;
		margin: .45rem 0 0 0;
	}

	.site-header-front-desktop .site-logo {
		font-size: 2rem;
		line-height: 3rem;
	}
	.header-bottom {
		height: $headerButtomHeightSM;
		
	}
	.mobile-user-nav {
		li {
			line-height: 2rem;
		}
	}
	.section-nav {
		text-align: left;
		.mobile-section-title a {
			line-height: 2rem;
		}
		a {
			line-height: 1;
			padding: 0 .625rem;
		}
		
		li {
			line-height: $headerButtomHeightSM;
			margin: 0;
			float: left;

			&:nth-child(1n+3) {
				width: 50%;
				float: left;
				margin: 0;
			}
			&:not(:first-child){
				a {
					line-height: $headerButtomHeightSM;
				}
			}
			
		}
		.mobile-section-title {
			float: left;
		}
		.main-section-link {
			float: none; 
			width: 100%;
			position: relative;
			font-weight: 600;
			clear: both;
			background-color: red;
			a {
				background-color: darken(#f9f9f9, 5%);
				// padding-left: .55rem;
				&:before {
					content: '\e800';
					font-family: b-icons;
					margin-left: -.45rem;
					float: left;
				}			
			}
		}
	}

	.main-navigation {
		
		
		&> ul:first-child {
			position: absolute;
			z-index: 99;
			right: 0;
			left: 0;
		}
		ul:first-child li {
			&:not(:first-child) {
				visibility: hidden;
				opacity: 0;
				transition: transform, opacity;
				transition-duration: .2s;
				font-size: .9rem;
				@extend .font-g1;
				&.active {
					a {
						color: $col-blue-1;
					}
				}
			}
			a {
				color: #333;
			}
		}

		ul.show-submenu {
			background-color: $background-color;
			box-shadow: 0 2px 2px rgba(0,0,0,.2);
			li:not(:first-child) {
				display: block;
				background: #f9f9f9;
				opacity: 1;
				visibility: visible;
			}
			


			.bicon-angle-down:before {
				transform: rotate(180deg);
			}
		}

	}
	
	.mobile-section-title {
		@extend .font-sans-alt;
		text-transform: uppercase;
		font-size: 1.5rem;
		max-width: 50%;
		line-height: 1.4;
		a {
			color: $col-blue-1!important;
			text-decoration: none;
			display: block;
			text-overflow: ellipsis;
			overflow: hidden;
			white-space: nowrap;
		}
		i {
			font-size: .8rem;
			vertical-align: middle;
			line-height: 1.5rem;
		}
	}
}
