.paywall-ver-1 {
	background-color: darken($background-color, 5%);
}

.btn-close-paywall-overlay {
	position: absolute;
	top: 0;
	right: 0;
	@extend .font-s2;
	line-height: 2rem;
	color: #999;
	font-size: .7rem;
	text-align: center;
	background-color: $background-color;
	&:before {
		content: 'LUK';
		padding-right: .2rem;
		font-weight: 500;
		line-height: 2rem;
		font-size: .7rem;
		float: left;
	}
}

.paywall {
	margin-left: auto;
	margin-right: auto;
	text-align: center;
	padding-top: 1.5rem;
	z-index: 15;
	position: relative;
	color: #333;
	

	h2 {
		font-size: 2.5rem;
		@extend .font-s1;
		position: relative;
	}
	p:not(.small) {
		font-size: 1.1rem;
		font-weight: 300;
		line-height: 1.5;
	}
	
	
	.img {
		 max-width: 16rem;
	}
	.alert {
		p {
			font-size: .85rem;
			@extend .font-s2;
		}
		a {
			text-decoration: underline;
		}
	}
	
}

.paywall-fade {
	&:after {
		position: absolute;
		content: '';
		left: 0rem;
		right: 0rem;
		top: -1rem;
		height: 1rem;
		background: radial-gradient(ellipse 50% 10px at 50% 100%,rgba(0,0,0,.15) 0,rgba(0,0,0,.0) 110%);
		z-index: -1;
	}
}
.paywall-soft {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: $background-color;
}

.paywall-hard {
	top: -2rem;
	background-color: $background-color;
	&:before {
		position: absolute;
		top: -6rem;
		height: 6rem;
		left: 0;
		right: 0;
		content: '';
		@include gradient-vertical( fade-out($background-color,1), $background-color, 0%, 100%);
		z-index: -2;
	}
}



.paywall-video {	
	@extend .flex-center;
	color: #fff!important;
	text-align: center;
	@extend .flex-center;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	padding: .625rem;
	.btn {
		margin-bottom: 1rem;
	}
	.icon {		
		&:before,
		&:after {
			width: 3.5rem;
			height: 3.5rem;
			line-height: 3.5rem;
			font-size: 2.625rem;
			top: .625rem;
			left: .625rem;
			text-align: center;
			z-index: 2;
			position: absolute;
		}
		&:before {
			box-shadow: 0 0 1rem rgba(0,0,0,.4);
			content: '';
			border-radius: 50%;
			background-color: $col-blue-1;
		}

		&:after {
			content: '\e823';
			transform: translateX(3%);
			color: #fff;
			font-family: b-icons;
		}
	}
	&:before {
		content: '';
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		position: absolute;
		@include gradient-vertical( rgba(0,0,0,.3), rgba(0,0,0,.95), 0%, 80%);
		z-index: 0;
	}
}


.article-cutoff {
	position: relative;
	.article-body {
		max-height: 30rem;	
		overflow: hidden;
	}
	&:before {
		position: absolute;
		content: '';
		left: -.2rem;
		right: -.2rem;
		bottom: 0rem;
		height: 30rem;
		@include gradient-vertical( fade-out($background-color,.9), $background-color, 0%, 50%);
		z-index: 10;
	}
}

.marketing-soft-paywall {
	padding: 1rem 0 0 0;
	position: relative;
	
}





@include media-breakpoint-only(xl) {
	.paywall-hard {
		margin-left: -8rem;
		margin-right: -8rem;
	}
	.tpl-article-longread {
		.paywall-hard{
			margin-left: -(35.938rem / 2);
			margin-right: -(35.938rem / 2);
		}
	}
}
@include media-breakpoint-only(lg) {
	.paywall {
		h2 {
			font-size: 1.8rem;
		}
	}
	.tpl-article-longread {
		.paywall-hard{
			margin-left: -10rem;
			margin-right: -10rem;
		}
	}
}

@include media-breakpoint-down(sm) {
	.paywall {
		h2 {
			font-size: 1rem;
		}
		p:not(.small) {
			font-size: .75rem;
		}
	}
	.paywall-video {
		p {
			margin: 0 0 .2rem;
		}
		.btn {
			margin-bottom: .625rem;
		}
		.icon {		
			&:before,
			&:after {
				width: 3.5rem * .4;
				height: 3.5rem * .4;
				line-height: 3.5rem * .4;
				font-size: 2.625rem * .4;
				top: .625rem;
				left: .625rem;
				text-align: center;
				z-index: 2;
				position: absolute;
			}
		}
	}
}